import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom';
import Css from './Covid.css'
const Covid = () => {

    //  useEffect to fetch the API data : 
    const [data, setData] = useState([]);
    const getCovidData = async () => {
        try {

            const res = await fetch('https://api.covid19india.org/data.json')

            const actualRes = await res.json()
            console.log(actualRes.statewise[0])
            setData(actualRes.statewise[0])
        }
        catch (err) {
            console.log(err);
        }
    }
    useEffect(() => {
        getCovidData();
    }, [])
    return (
        <div>
            <div className="Live-heading">
                <span className="dot">ðŸ”´ LIVE</span>
            </div>

            <div className="main-head">
                COVID-19 TRACKER
            </div>

            <div className="full-page">


                <div className="cards">

                    <div className="card-country card"><span className = "tags">COUNTRY</span>INDIA</div>
                    <div className="card-Recovered card">
                        <span className = "tags">TOTAL RECOVERED</span>{data.recovered}</div>
                    <div className="card-confirmed card"><span className = "tags">TOTAL CONFIRMED</span>{data.confirmed}</div>
                    <div className="card-Deaths card"><span className = "tags">TOTAL DEATHS</span>{data.deaths}</div>
                    <div className="card-Active card"><span className = "tags">TOTAL ACTIVE CASES</span>{data.active}</div>
                    <div className="card-updated card"><span className = "tags">LAST UPDATED</span>{data.lastupdatedtime}</div>
                </div>







            </div>
        </div>
    )
}

export default Covid
